import React, { Component } from 'react';
import Title from './title'
import { floor4 } from '../../data/zhibo'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            floor4,
            list: [
                {
                    name: '',
                    remark: ''
                }, {
                    name: '',
                    remark: ''
                }, {
                    name: '',
                    remark: ''
                },
            ]
        }
    }
    render() {
        return <div className="floor6 floor section-box">
            <Title title="" letter="SERVICE FUNCTION" />
            <div className="more-list">
                {
                    this.state.floor4.moreList.map((v, i) => <span className={i % 7 % 2 === 0 ? 'item-bg' : ''} key={i}>{v}</span>)
                }
            </div>
            <div className="list">
                {
                    this.state.list.map((v, i) => <span key={i}>
                        <img src={require(`../../assets/home/qudao_0${7+i}.png`)} alt="" />
                        <div>{v.name}</div>
                        <div>{v.remark}</div>
                    </span>)
                }
            </div>
            <div className="btn-check-all btn"></div>
        </div>
    }
}